* {
  box-sizing: border-box;
}

p {
  margin: 0;
  padding: 0;
}

#search-form {
  background-color: rgba(63, 54, 234, .953);
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
  padding: 10px 0;
  display: flex;
}

.btn-icon {
  color: #000;
}

.search-form-input {
  width: 220px;
  outline: none;
  padding: 4px 5px;
}

.form-btn {
  width: 30px;
  height: 30px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.photo-card {
  border-radius: 0 0 4px 4px;
  flex-basis: calc(25% - 20px);
  margin-bottom: 10px;
  margin-right: 20px;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.photo-card:nth-child(4n+4) {
  margin-right: 0;
}

.gallery-image {
  max-width: 100%;
  height: 191px;
  width: 300px;
  object-fit: cover;
  display: block;
  overflow: hidden;
}

.info {
  justify-content: space-evenly;
  align-items: center;
  padding: 10px 0;
  line-height: 20px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  padding: 0 4px;
  font-size: 12px;
  display: flex;
}

.load-more {
  color: #fff;
  background-color: rgba(63, 54, 234, .953);
  border: none;
  border-radius: 4px;
  margin-bottom: 10px;
  margin-left: auto;
  margin-right: auto;
  padding: 5px 10px;
  display: block;
}

.load-more.is-hidden {
  display: none;
}

